import React from 'react';
import { Link } from 'react-router-dom'
import './style.css'

function Navbar({userId, logout}) {
  return (
    <div className='navbar'>
      <p className='nav-p'>
        { userId ?
          <Link id='signup-link' to='/signup'>Sign Up</Link> : 
          <Link id='signin-link' to='/signin'>Sign In</Link> 
        }
        { userId && <button className='logout-btn' onClick={logout}>Logout</button> }
        </p>
    </div>
  ); 
};

export default Navbar