import React, { useState } from 'react'
import Header from '../../components/Header';
import Footer from '../../components/Footer';
import { Link, useNavigate } from 'react-router-dom';
import API from '../../utils/API';
import './style.css';

function SignIn(props) {
  const navigate = useNavigate();
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
      
  const handleChange = e => {
    if (e.target.name === "username") {
      setUsername(e.target.value)
    } else if (e.target.name === "password") {
      setPassword(e.target.value)
    }
  }

  const submitHandler = e => {
    e.preventDeafault()
    API.signin({
      username:username,
      password:password
    })
    .then(data => {
      props.setUserId(data.user.id)
      props.setUsername(data.user.username)
      props.setToken(data.token)
      localStorage.setItem("token", data.token)

      navigate('/profile')
    }).catch(err => {
      alert("Unable to Sign Up")
    })
  }

  return (
    <div>
      <Header />
        <main className='main-form'>
          <h1>Sign In</h1>
          <form onSubmit={submitHandler}>
            <input id='si-user' name='username' placeholder='username' value={username} onChange={handleChange}></input>
            <input id='si-pass' name='password' type='password' placeholder='password' value={password} onChange={handleChange}></input>
            <button id='si-submit'>Sign In</button>
          </form>
          <h2>Don't have an account? <Link to='/signup'>Sign Up</Link></h2>
        </main>
      <Footer />
    </div>
  );
};

export default SignIn